  position: relative;
  height: 34px;
  width: 80px;
  min-height: 34px;
  min-width: 80px;
  color: #fff;
  font-size: 12px;
  font-weight: bold;
  line-height: 20px;
  text-align: center;
  background: #D7D7D7;
  border: 2px solid #D7D7D7;
  text-transform: uppercase;
  font-family: Helvetica, Arial, sans-serif;
  transition: 0.3s ease-out;
  border-radius: 3px;
}
label.switch > label + span {
  display: inline-block;
  padding-left: 5px;
  position: relative;
  top: -7px;
}
label.switch > label:before {
  content: attr(data-off);
  position: absolute;
  top: 1px;
  right: 14px;
  line-height: 28px;
}
label.switch > label:after {
  content: "";
  margin: 1px;
  width: 28px;
  height: 28px;
  display: block;
  background: #fff;
  -webkit-border-radius: 2px;
  border-radius: 2px;
}
label.switch > input {
  -webkit-appearance: none;
  position: absolute;
  width: inherit;
  height: inherit;
  opacity: 0;
  left: 0;
  top: 0;
}
label.switch > input:focus {
  outline: none;
}
label.switch > input:focus + label {
  color: #fff;
  border-color: #C7C7C7;
  background: #C7C7C7;
}
label.switch > input:focus + label:after {
  background: #fff;
}
label.switch > input:checked + label {
  border-color: #999;
  background: #999;
  padding-left: 46px;
  color: white;
}
label.switch > input:checked + label:before {
  content: attr(data-on);
  left: 14px;
  top: 1px;
  right: initial;
}
label.switch > input:checked + label:after {
  margin: 1px;
  width: 28px;
  height: 28px;
  background: white;
}
label.switch.switch-primary > input:checked + label {
  background: #3498db;
  border-color: #3498db;
}
label.switch.switch-primary > input:checked + label:after {
  color: #3498db;
}
label.switch.switch-primary > input:checked:focus + label {
  background: #3498db;
  border-color: #3498db;
}
label.switch.switch-success > input:checked + label {
  background: #70AB4F;
  border-color: #70AB4F;
}
label.switch.switch-success > input:checked + label:after {
  color: #70AB4F;
}
label.switch.switch-success > input:checked:focus + label {
  background: #70AB4F;
  border-color: #70AB4F;
}
label.switch.switch-info > input:checked + label {
  background: #3bafda;
  border-color: #3bafda;
